<template>
  <div id="app">
    <keep-alive>
      <router-view v-wechat-title="$route.meta.title" v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-wechat-title="$route.meta.title" v-if="!$route.meta.keepAlive"></router-view>
    <Loading v-if="loadingShow" type="circular"></Loading>
  </div>
</template>
<script>
import loading from '@/components/loading.vue';

export default {
  name: 'App',
  provide() {
    return {
      app: this
    }
  },
  data() {
    return {
      loadingShow: 0,
    }
  },
  components: {
    'Loading': loading
  },
  methods: {
    showLoading() {
      this.loadingShow = 1;
    },
    hideLoading() {
      this.loadingShow = 0;
    }
  },
}

</script>
<style>
</style>



