export default [
  // 默认页
  {
    path: '/',
    redirect: '/login',
  },
  // 404
  {
    path: '*',
    name: '404',
    component: resolve => require(['@/components/404'], resolve),
    meta: {
      title: '404'
    }
  },
  // 
  {
    path: '/login',
    name: 'login',
    component: resolve => require(['@/views/login/login'], resolve),
    meta: {
      title: '登录'
    }
  },
  {
    path: '/fillinfo',
    name: 'fillinfo',
    component: resolve => require(['@/views/fillinfo/fillinfo'], resolve),
    meta: {
      title: '完善信息'
    }
  },
  {
    path: '/info',
    name: 'info',
    component: resolve => require(['@/views/info/info'], resolve),
    meta: {
      title: '参赛信息'
    }
  },
  {
    path: '/match',
    name: 'match',
    component: resolve => require(['@/views/match/match'], resolve),
    meta: {
      title: '对战信息'
    }
  },
  {
    path: '/rule',
    name: 'rule',
    component: resolve => require(['@/views/rule/rule'], resolve),
    meta: {
      title: '赛事规则'
    }
  },
  {
    path: '/list',
    name: 'list',
    component: resolve => require(['@/views/list/list'], resolve),
    meta: {
      title: '历史赛事'
    }
  },

]



